import Vue from 'vue'

export const login = (formData = {}) => {
  return new Promise((resolve, reject) => {
    Vue.http.get('/static/user.json')
      .then(({ data }) => {
        if (
          formData.username === data.username &&
          formData.password === data.password
        ) {
          resolve()
        } else {
          reject(new Error('用户名或密码错误'))
        }
      })
      .catch(res => {
        reject(new Error('用户名或密码错误'))
      })
  })
}



