import Vue from 'vue'
import Router from 'vue-router'

import store from '@/store'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/pages/Login')
    },
    {
      path: '/',
      component: () => import('@/pages/Home'),
      children: [
        {
          path: '',
          name: 'BaseInfo',
          component: () => import('@/pages/others/BaseInfo'),
          meta: {
            auth: true
          }
        },
        {
          path: 'wifiSetting',
          name: 'WifiSetting',
          component: () => import('@/pages/others/WifiSetting'),
          meta: {
            auth: true
          }
        },
        {
          path: 'volumeSetting',
          name: 'VolumeSetting',
          component: () => import('@/pages/others/VolumeSetting'),
          meta: {
            auth: true
          }
        },
        {
          path: 'temperatureThreshold',
          name: 'TemperatureThreshold',
          component: () => import('@/pages/others/TemperatureThreshold'),
          meta: {
            auth: true
          }
        },
        {
          path: 'cameraDistanceCompensation',
          name: 'CameraDistanceCompensation',
          component: () => import('@/pages/others/CameraDistanceCompensation'),
          meta: {
            auth: true
          }
        },
        {
          path: 'cameraExplore',
          name: 'CameraExplore',
          component: () => import('@/pages/others/CameraExplore'),
          meta: {
            auth: true
          }
        },
        {
          path: 'FFCCompensation',
          name: 'FFCCompensation',
          component: () => import('@/pages/others/FFCCompensation'),
          meta: {
            auth: true
          }
        },
        {
          path: 'FFCCalibrationSwitch',
          name: 'FFCCalibrationSwitch',
          component: () => import('@/pages/others/FFCCalibrationSwitch'),
          meta: {
            auth: true
          }
        },
        {
          path: 'areaAdjusting',
          name: 'AreaAdjusting',
          component: () => import('@/pages/others/AreaAdjusting'),
          meta: {
            auth: true
          }
        },
        {
          path: 'faceAdjusting',
          name: 'FaceAdjusting',
          component: () => import('@/pages/others/FaceAdjusting'),
          meta: {
            auth: true
          }
        },
        {
          path: 'localImages',
          name: 'LocalImages',
          component: () => import('@/pages/others/LocalImages'),
          meta: {
            auth: true
          }
        }
      ]
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})

router.beforeEach(async (to, from, next) => {
  if (to.meta.auth) {
    const userInfo = store.getters['login/getUserInfo']

    if (!(userInfo || {}).username) {
      return next('/login')
    }
  }

  const settingInfo = store.getters.getSettingInfo
  if (settingInfo.nodata && to.meta.auth) {
    try {
      await store.dispatch('getAllSetting')
    } catch (res) {}
  }

  next()
})

router.afterEach((to, from) => {
  store.commit('changeActiveMenu', to.path)
})

export default router



