import Vue from 'vue'

export const getAllSetting = () => {
  return Vue.http.get('/setting')
}

export const setWifi = (formData) => {
  return Vue.http.post('/setting/set/wifi', formData)
}

export const setVoice = (formData) => {
  return Vue.http.post('/setting/set/voice', formData)
}

export const setTemperature = (formData) => {
  return Vue.http.post('/setting/set/temperature', formData)
}

export const setCameraExplore = (formData) => {
  return Vue.http.post('/setting/set/camera_explore', formData)
}

export const setFFCCompensation = (formData) => {
  return Vue.http.post('/setting/set/FFC_compensation', formData)
}

export const setFFCBlackCalibrationn = (formData) => {
  return Vue.http.post('/setting/set/FFC_black_calibrationn', formData)
}

export const setFFCAvgCalibrationn = (formData) => {
  return Vue.http.post('/setting/set/FFC_avg_calibrationn', formData)
}
export const setTemperDistance = (formData) => {
  return Vue.http.post('/setting/set/temper_distance', formData)
}

export const photoPreview = () => {
  return Vue.http.post('/setting/set/photo_preview')
}

export const tempLocation = (formData) => {
  return Vue.http.post('/setting/set/temp_location', formData)
}

export const getValidArea = (formData) => {
  return Vue.http.post('/setting/set/get_valid_area', formData)
}

export const setValidArea = (formData) => {
  return Vue.http.post('/setting/set/set_valid_area', formData)
}

export const queryData = (formData) => {
  return Vue.http.post('/setting/set/query_data', formData)
}



