import {
  getAllSetting,
  setWifi,
  setVoice,
  setTemperature,
  setCameraExplore,
  setFFCCompensation,
  setFFCBlackCalibrationn,
  setFFCAvgCalibrationn,
  setTemperDistance,
  photoPreview,
  tempLocation,
  getValidArea,
  setValidArea,
  queryData
} from '@/api'

const MENU_SYMBOL = 'ACTIVE_MENU'

export const state = {
  activeMenu: sessionStorage.getItem(MENU_SYMBOL) || '/',
  settingInfo: {
    nodata: 1
  }
}

export const getters = {
  getActiveMenu: state => state.activeMenu,
  getSettingInfo: state => state.settingInfo
}

export const mutations = {
  changeActiveMenu (state, activeMenu) {
    state.activeMenu = activeMenu
    sessionStorage.setItem(MENU_SYMBOL, activeMenu)
  },
  changeSettingInfo (state, settingInfo) {
    state.settingInfo = settingInfo
  }
}

export const actions = {
  getAllSetting ({ commit }) {
    return new Promise((resolve, reject) => {
      getAllSetting()
        .then(res => {
          commit('changeSettingInfo', res.data)
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setWifi ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setWifi(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setVoice ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setVoice(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setTemperature ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setTemperature(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setCameraExplore ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setCameraExplore(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setFFCCompensation ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setFFCCompensation(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setFFCBlackCalibrationn ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setFFCBlackCalibrationn(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setFFCAvgCalibrationn ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setFFCAvgCalibrationn(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setTemperDistance ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setTemperDistance(formData)
        .then(res => {
          commit('changeSettingInfo', {...state.settingInfo, ...formData})
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  photoPreview ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      photoPreview(formData)
        .then(res => {
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  tempLocation ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      tempLocation(formData)
        .then(res => {
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  getValidArea ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      getValidArea(formData)
        .then(res => {
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  setValidArea ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      setValidArea(formData)
        .then(res => {
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  },
  queryData ({ commit, state }, formData) {
    return new Promise((resolve, reject) => {
      queryData(formData)
        .then(res => {
          resolve(res)
        })
        .catch(res => {
          reject(res)
        })
    })
  }
}



